#home {
  /* Spiration Dark from  Toptal Subtle Patterns
     * at https://www.toptal.com/designers/subtlepatterns/spiration-dark/
     * by Monika*/
  background-image: url("../assets/spiration.png");
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: transparent;
  -webkit-animation: moveDown 5s linear infinite;
          animation: moveDown 5s linear infinite; }

@-webkit-keyframes moveDown {
  from {
    background-position: top; }
  to {
    background-position: bottom; } }

@keyframes moveDown {
  from {
    background-position: top; }
  to {
    background-position: bottom; } }

#homeCenter {
  color: white;
  padding: 0;
  top: 40%;
  left: 48%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
  font-family: "Roboto", sans-serif; }

#homeTitle {
  float: left; }

.anim {
  width: 100%;
  height: 100%;
  z-index: -1; }

.typed-cursor {
  color: white;
  opacity: 1;
  animation: typedjsBlink 0.7s infinite;
  -webkit-animation: typedjsBlink 0.7s infinite; }

@keyframes typedjsBlink {
  50% {
    opacity: 0.0; } }

@-webkit-keyframes typedjsBlink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.0; }
  100% {
    opacity: 1; } }

#homeCenter span.typed-cursor {
  font-size: 450%; }

.typed-fade-out {
  opacity: 0;
  -webkit-transition: opacity .25s;
  -o-transition: opacity .25s;
  transition: opacity .25s;
  -webkit-animation: 0;
  animation: 0; }

.anim {
  color: white; }

@media (min-width: 600px) and (max-width: 1024px) {
  #home h2 {
    font-size: 275%; }
  #homeCenter span.typed-cursor {
    font-size: 400%; }
  .anim {
    display: none; } }

@media (min-width: 301px) and (max-width: 599px) {
  #home h2 {
    font-size: 200%;
    white-space: pre; }
  #homeCenter span.typed-cursor, .anim {
    display: none; } }

@media (min-width: 0px) and (max-width: 300px) {
  .anim, #homeCenter {
    display: none; } }



