@import url(../../node_modules/slick-carousel/slick/slick.css);
@import url(../../node_modules/slick-carousel/slick/slick-theme.css);
#projects {
  font-family: "Roboto", sans-serif;
  height: 100vh; }

#projectsHeader {
  display: none; }

#projectsHeader p {
  margin: 0; }

#projectsTopSlider {
  width: 100%;
  height: 30%;
  top: 15%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: relative;
  text-align: center; }

#projectsGithub button {
  background-color: transparent;
  -webkit-box-shadow: none;
          box-shadow: none; }

#githubButton {
  padding: 0 !important;
  margin: 0 0.5rem !important;
  border-radius: 50% !important; }

#projectsTopSlider h2 {
  margin: 0; }

#projectsGithub, #projectsTopSlider h2 {
  color: #FFAB40 !important; }

.card {
  color: #C12101 !important; }

@-webkit-keyframes moveRight {
  from {
    background-position: left; }
  to {
    background-position: right; } }

@keyframes moveRight {
  from {
    background-position: left; }
  to {
    background-position: right; } }

@-webkit-keyframes moveLeft {
  from {
    background-position: right; }
  to {
    background-position: left; } }

@keyframes moveLeft {
  from {
    background-position: right; }
  to {
    background-position: left; } }

#projectsBackTop {
  /* Seigaiha Variation
     * Original from  Toptal Subtle Patterns
     * at https://www.toptal.com/designers/subtlepatterns/seigaiha/
     * by Olga Libby*/
  background-image: url("../assets/seigaiha-strong-blue.png");
  height: 60vh;
  position: relative;
  /*animation: moveRight 10s linear infinite;*/ }

#projectsBackBot {
  /* Seigaiha Variation
     * Original from  Toptal Subtle Patterns
     * at https://www.toptal.com/designers/subtlepatterns/seigaiha/
     * by Olga Libby*/
  background-image: url("../assets/seigaiha-strong-white.png");
  height: 40vh;
  /*animation: moveLeft 10s linear infinite;*/ }

#projectsBackground {
  position: absolute;
  height: 100vh; }

#projectsForeground {
  position: relative;
  height: 100vh;
  z-index: 2;
  background-color: transparent; }

#projectHolder {
  top: 65%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute; }

#projectsStart {
  position: relative;
  top: 5%; }

.slick-arrow {
  display: none !important; }

@media (min-width: 1025px) {
  #projectsTopSlider {
    left: -2%; }
  #projectHolder {
    top: 35%;
    left: 17%;
    -webkit-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
            transform: translate(0, 0);
    position: absolute; } }

@media (min-width: 900px) and (max-width: 1024px) {
  #projectHolder {
    left: 53%; }
  #projectsTopSlider {
    left: 0; } }

@media (min-width: 768px) and (max-width: 1024px) {
  #projectsTopSlider {
    top: 30%;
    left: 50%; } }

@media (min-width: 300px) and (max-width: 1024px) and (orientation: portrait) {
  #projectsTopSlider {
    top: 35%; }
  #projectsGithub {
    font-size: 1.20rem; } }

@media (min-width: 300px) and (max-width: 900px) and (orientation: landscape) {
  #projectsTopSlider {
    display: none; }
  #projectHolder {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50, -50);
        -ms-transform: translate(-50, -50);
            transform: translate(-50, -50);
    position: absolute; }
  .slick-list {
    width: 265px;
    height: 265px;
    left: 50%;
    -webkit-transform: translateX(-50%) !important;
        -ms-transform: translateX(-50%) !important;
            transform: translateX(-50%) !important; }
  .card.small {
    height: 250px; } }

@media (min-width: 0px) and (max-width: 300px) {
  #projectsHeader, #projectHolder, #projectsTopSlider {
    display: none; } }



